.clearfix {
	&:before,
	&:after {
		content: " ";
		display: table;
	}
	&:after {
		clear: both;
	}
}

.img-responsive {
	display: inline-block;
	max-width: 100%;
	height: auto;
	vertical-align: middle;
}

.fz {
	&-12 { font-size: 12px; }
	&-14 { font-size: 14px; }
	&-16 { font-size: 16px; }
}

.lh {
	&-1 { line-height: 1; }
	&-1-5 { line-height: 1.5; }
	&-2 { line-height: 2; }
}

.m {
	@include d('5') { margin: $sm-c; }
	@include d('r5') { margin-right: $sm-c; }
	@include d('l5') { margin-left: $sm-c; }
	@include d('t5') { margin-top: $sm-c; }
	@include d('b5') { margin-bottom: $sm-c; }
	@include d('15') { margin: $c; }
	@include d('r15') { margin-right: $c; }
	@include d('l15') { margin-left: $c; }
	@include d('t15') { margin-top: $c; }
	@include d('b15') { margin-bottom: $c; }
}

.p {
	@include d('5') { padding: $sm-c; }
	@include d('r5') { padding-right: $sm-c; }
	@include d('l5') { padding-left: $sm-c; }
	@include d('t5') { padding-top: $sm-c; }
	@include d('b5') { padding-bottom: $sm-c; }
	@include d('15') { padding: $c; }
	@include d('r15') { padding-right: $c; }
	@include d('l15') { padding-left: $c; }
	@include d('t15') { padding-top: $c; }
	@include d('b15') { padding-bottom: $c; }
}

.hidden {
	@include d('xs') { @include respond-to('xs') { display: none; } }
	@include d('smp') { @include respond-to('sm-p') { display: none; } }
	@include d('uptomd') { @include respond-to('upto-md') { display: none; } }
}

.center-ib-content {
	&:before {
		content: '';
		display: inline-block;
		height: 100%;
		vertical-align: middle;
	}
}

.text {
	&-right { text-align: right; }
	&-left { text-align: left; }
	&-center { text-align: center; }
}